.hidden {
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  pointer-events: none;
  width: 0;
  height: 0;
  display: block;
}

.image-load {
  position: relative;
}

.image-load:not(.loaded):before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: url("../_img/preloader.svg") no-repeat center center;
}

.image-load > :first-child {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.3s;
}

.image-load.loaded > :first-child {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.h-light-image img {
  transition: opacity 0.3s, -webkit-filter 0.3s;
  transition: filter 0.3s, opacity 0.3s;
  transition: filter 0.3s, opacity 0.3s, -webkit-filter 0.3s;
}

.h-light-image:hover img {
  -webkit-filter: brightness(106%);
  filter: brightness(106%);
}

.h-center {
