input[type=checkbox] {
  display: none;
}

.checkbox-label {
  display: block;
  color: #000;
  position: relative;
  margin: 0 0 21px 32px;
  cursor: pointer;
}

.checkbox-label:before {
  content: '';
  position: absolute;
  display: block;
  left: -32px;
  top: 2px;
  width: 14px;
  height: 14px;
  border: 1px solid #d7d7d7;
}

.checkbox-label:after {
  content: '';
  display: block;
  position: absolute;
  left: -29px;
  top: -1px;
  width: 0;
  height: 14px;
  background: url("../_img/sprite.svg?v=198") no-repeat 0 10.99476439790576%/14px auto transparent;
  transition: width 0.2s;
}

input[type=checkbox]:checked + .checkbox-label:after {
  width: 14px;
}

.c-badge {
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 40px;
  overflow: hidden;
  font-size: 12px;
  text-align: center;
  z-index: 1;
}

.c-badge:before,
.c-badge:after {
  content: '';
  position: absolute;
  display: block;
  border-radius: 40px;
}

.c-badge:before {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.c-badge:after {
  line-height: 3.3em;
  top: 1px;
  left: 1px;
  bottom: 1px;
  right: 1px;
  z-index: 2;
  background: #fff;
}

.c-badge--sale {
  color: #ea6666;
}

.c-badge--sale:before {
  background: linear-gradient(135deg, #ee9185 0%, #e8c15f 100%);
}

.c-badge--sale:after {
  content: 'Sale';
}

.c-badge--new {
  color: #04be45;
}

.c-badge--new:before {
  background: linear-gradient(135deg, #2ccd5d 0%, #16e4d9 100%);
}

.c-badge--new:after {
  content: 'New';
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9999;
  text-align: center;
  overflow-x: hidden;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.3s, visibility 0.3s;
  padding-top: 10px;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.modal.is-open .modal__inner {
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
}

.modal:before {
  content: '';
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-color: rgba(0,0,0,0.5);
}

.modal:after {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  content: "";
}

.modal__inner {
  position: relative;
  border-radius: 3px;
  z-index: 10;
  vertical-align: middle;
  display: inline-block;
  background-color: #fff;
  padding: 40px 20px 20px;
  max-width: 90%;
  text-align: left;
  -webkit-transform: scale(0.9);
  -ms-transform: scale(0.9);
  transform: scale(0.9);
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.modal__close {
  position: absolute;
  top: 12px;
  left: calc(50% - 8px);
  width: 17px;
  height: 17px;
}

.modal__close:after {
  content: '';
  position: absolute;
  top: -12px;
  bottom: -5px;
  left: -30px;
  right: -30px;
}

.modal__title {
  text-align: center;
  font-size: 14px;
  color: #000;
  margin: 0 0 20px;
  text-transform: uppercase;
  font-family: 'RobotoBold';
  letter-spacing: 0.1em;
}

.modal__content--mobile {
  width: 710px;
  max-width: 100%;
}

